<template lang='pug'>
    div.div1
        p Returnfjdkslfjinvireiofsdjvkldfjisdfhiodfsdlkjfdlkasfjisdaofhjiweofhesjfldsjflkdsjfoiwejflisejflkdsjflksdjfadli;sjfdaslk;fjasdlk
        el-card
            el-tabs(v-model='active', type='card')
                el-tab-pane(label='123', name='1') {{request}}
                el-tab-pane(label='456', name='2') 2134123563451365623466234562354353345
                el-tab-pane(label='789', name='3') @#$^%$$%^$&^%&^#$%@#$%%^*$%^&^%&^$%^#
                el-tab-pane(label='000', name='4') ASKLFJJKLWEQRFOIDSMKDSFKLADSJFEWJWEIO
</template>

<script>
import cityCard from '../../common/CityCard.vue'
export default {
    components: {
        cityCard
    },
    data () {
        return {
            active: '1',
            request: undefined,
            db: undefined
        }
    },
    created () {
        console.log(this.request, this.db)
        let _this = this
        if (!window.indexedDB) {
            window.alert('浏览器不支持IndexedDB，建议更换为谷歌浏览器')
        }
        this.request = window.indexedDB.open('test')
        this.request.onerror = (event) => {
            alert('what happended')
        }
        this.request.onsuccess = (event) => {
            _this.db = _this.request.result
        }
        this.$http.post('/jd/searchHotel/', {
            cityCode: '上海',
            begin: '2018-02-08',
            end: '2018-02-09',
            pageCount: 1,
            rowCount: 10,
            saleStatus: 1
        }).then((res) => {
            console.log(res.data)
        }).catch((error) => {
            console.log(error)
        })
    }
}
</script>

<style scoped>
    .div1 {
        height: 1000px;
        background-color: red;
        padding: 200px;
    }
    p {
        background: tan;
        border-radius: .8em;
        padding: 1em;
        box-shadow: 0 0 0 .6em #655;
        outline: .6em solid #655;
        /* outline: 5px dashed #123; */
        /* background-clip: padding-box;
        box-shadow: 0 0 0 10px #655; */
        margin-bottom: 20px;
    }
</style>



